  font-weight: 700;
  font-style: normal;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Raleway", "Arial", sans-serif;
  font-weight: 400;
  overflow-x: hidden;
  background-color: #fafafa;
}
.wrapper {
  margin: 0 auto;
  padding: 0;
  width: 100%;
  box-sizing: border-box;
  z-index: 1;
}
.visually-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap;
}
.main {
  z-index: 1;
}
ul {
  list-style: none;
  padding: 0;
}
* {
  font-variant-numeric: lining-nums;
  -moz-font-feature-settings: "lnum";
  -webkit-font-feature-settings: "lnum";
  font-feature-settings: "lnum";
}
.page-header__wrapper {
  display: flex;
  justify-content: space-between;
}
.page-header__logo-header {
  margin-top: 20px;
  margin-left: 20px;
}
.page-nav__toggle {
  width: 90px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #444;
  border: none;
  pointer-events: initial;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.76), 0 3px 6px rgba(0, 0, 0, 0.83);
  position: relative;
  z-index: 2;
}
.page-nav__svg {
  fill: #b8d053;
  display: none;
}
.page-nav__svg--cross {
  fill: #ff424a;
}
.page-nav__wrapper {
  position: fixed;
  top: 0;
  left: -240px;
  z-index: 2;
  width: 240px;
  height: 100vh;
  background-color: #444444;
  box-shadow: 0 3px 6px rgba(184, 208, 83, 0.76), 0 3px 6px rgba(184, 208, 83, 0.83);
  padding-top: 30px;
  transition: ease-in-out 0.7s;
}
.page-nav__contacts {
  margin-top: 10px;
  display: flex;
  flex-direction: column-reverse;
}
.page-nav__contacts .contacts__item {
  font-size: 18px;
  padding-left: 30px;
  padding-top: 15px;
  padding-bottom: 15px;
}
.page-nav__contacts .contacts__callback {
  text-decoration: none;
  display: block;
  text-align: center;
  width: 160px;
  padding: 10px;
  background-color: #fff;
  border-radius: 15px;
  color: #b8d053;
}
.page-nav__contacts .contacts__phonelink {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 21px;
  font-weight: 600;
  color: #fff;
}
.page-nav__contacts .contacts__phonelink span {
  color: #b8d053;
}
.nav-list {
  margin: 0;
}
.nav-list__item {
  padding-left: 30px;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 18px;
}
.nav-list__item:hover,
.nav-list__item:focus {
  background-color: #ffffff;
}
.nav-list__link {
  display: block;
  width: 100%;
  height: 100%;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  color: #b8d053;
}
.page-nav__wrapper--show {
  left: 0;
  transition: ease-in-out 0.7s;
}
.svg-show {
  display: block;
}
.intro {
  background-image: url("../img/index-intro-bg_mobile.png");
  background-repeat: no-repeat;
  background-position: bottom 0 left 50%;
  background-size: cover;
  min-height: 462px;
  padding: 30px 20px;
  position: relative;
}
.intro__title {
  margin: 0;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 36px;
  max-width: 280px;
  color: #fff;
}
.intro__subtitle {
  color: #fff;
  line-height: 24px;
  margin-top: 10px;
  max-width: 280px;
}
.intro__button {
  width: 260px;
  height: 60px;
  border-radius: 10px;
  background-color: #ff424a;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
}
.intro__more-link {
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 600;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.production__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.production__title {
  font-size: 24px;
  max-width: 140px;
  text-align: center;
  margin-top: 30px;
  margin-bottom: 30px;
  color: #444444;
}
.production__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.production__item {
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: #fff;
  margin-bottom: 60px;
  border-radius: 0 0 10px 10px;
}
.production__item .image {
  background-color: #444444;
  width: 280px;
  height: 240px;
  position: relative;
}
.production__item .image img {
  position: absolute;
  bottom: -22px;
  right: 50%;
  transform: translateX(50%);
}
.production__item::before {
  content: '';
  position: absolute;
  bottom: -30px;
  left: 0;
  right: 0;
  z-index: -1;
  height: 30px;
  border-radius: 100%;
  background-color: #000;
  filter: blur(15px);
  will-change: filter;
}
.production .description {
  padding-left: 20px;
}
.production__name {
  color: #b8d053;
  max-width: 260px;
  margin-top: 30px;
}
.production__button {
  border: none;
  width: 240px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  color: #fff;
  background-color: #b8d053;
  font-size: 18px;
  font-weight: 600;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
  position: relative;
}
.production__button::before {
  content: '';
  position: absolute;
  bottom: -30px;
  left: 0;
  right: 0;
  z-index: -1;
  height: 30px;
  border-radius: 100%;
  background-color: rgba(184, 208, 83, 0.5);
  filter: blur(15px);
  will-change: filter;
}
