.outcome {
  position: relative;
  background-color: #444444;
  padding-left: 20px;
  padding-bottom: 50px;
  margin-top: 20px;
}
.outcome::before {
  content: '';
  width: 90%;
  height: 200px;
  background-image: url("../img/logo-dark.png");
  background-repeat: no-repeat;
  background-position: 0 100%;
  background-size: contain;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
.outcome__title {
  color: #fff;
  display: inline-block;
  margin: 0;
  margin-top: 20px;
}
.outcome__item {
  margin-left: 45px;
  position: relative;
  margin-bottom: 20px;
  max-width: 240px;
  color: #fff;
  font-weight: 600;
}
.outcome__item::before {
  content: url(../../img/logo-dots.png);
  width: 35px;
  height: auto;
  position: absolute;
  top: 50%;
  left: -45px;
  transform: translateY(-85%);
}
.why {
  position: relative;
  overflow: hidden;
}
.why::before {
  content: url("../img/dekor-kv.png");
  position: absolute;
  top: 200px;
  z-index: -1;
}
.why::after {
  content: url("../img/dekor-kv.png");
  position: absolute;
  top: 600px;
  right: 0;
  transform: rotate(180deg);
  z-index: -1;
}
.why__title {
  text-align: center;
  margin-top: 30px;
  text-transform: uppercase;
  color: #ff424a;
}
.why__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 40px;
}
.why__item {
  width: 240px;
  height: 290px;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  border-radius: 15px;
  padding: 20px 20px;
  position: relative;
  background-color: #fff;
}
.why__item::before {
  content: url("../img/list--one.png");
  width: 270px;
  position: absolute;
  z-index: -1;
}
.why__item:first-child::before {
  top: -120px;
  left: -45px;
}
.why__item:nth-child(2)::before {
  top: -145px;
  left: 20px;
  transform: scaleX(-1);
}
.why__item:nth-child(3)::before {
  top: -150px;
  left: -80px;
}
.why__item:nth-child(4)::before {
  top: -100px;
  left: 200px;
}
