.result {
  margin-left: 35px;
}
.result__reduction {
  color: #444444;
}
.result__reduction span {
  display: inline-block;
  color: #ff424a;
  margin-top: 5px;
  font-weight: 700;
}
.result__increase {
  color: #b8d053;
  font-weight: 700;
}
.result__increase span {
  font-size: 21px;
  display: inline-block;
  margin-top: 5px;
}
.footer {
  background-color: #444444;
  padding: 30px 20px;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
}
.footer__logo {
  display: block;
  margin: 0 auto;
}
.contacts {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.contacts__phone {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  margin-right: 20px;
}
.contacts__phone p {
  font-size: 16px;
  color: #b8d053;
  font-weight: 600;
  margin: 0;
  margin-bottom: 10px;
}
.contacts__phone a {
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 5px;
}
.contacts__email {
  display: flex;
  flex-direction: column;
}
.contacts__email p {
  font-size: 16px;
  color: #b8d053;
  font-weight: 600;
  margin: 0;
  margin-bottom: 10px;
}
.contacts__email a {
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 5px;
}
.contacts__social {
  margin-top: 20px;
}
.contacts__social .vk {
  margin-right: 10px;
}
.contacts__social img {
  width: 30px;
  height: 30px;
}
.contacts__social a {
  text-decoration: none;
}
